<style scoped>
  .slide-trans-enter-active {
    transition: all .5s;
  }
  .slide-trans-enter {
    transform: translateX(700px);
  }
  .slide-trans-old-leave-active {
    transition: all .5s;
    transform: translateX(-700px);
  }
  .slide-show {
    position: relative;
    margin: 0 auto;
    height: 550px;
    border: 3px solid #333;
  }
  .slide-show h2 {
    position: absolute;
    width: 100%;
    color: #fff;
    background: #000;
    opacity: .5;
    bottom: 0;
    margin: 0;
    height: 32px;
    padding-left: 15px;
  }
  .slide-img {
    width: 100%;
  }
  .slide-img img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }
  .slide-pages {
    position: absolute;
    bottom: 0;
    margin-bottom: 5px;
    right: 15px;
  }
  .slide-pages li {
    display: inline-block;
    padding: 0 10px;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
  }
  .slide-pages li .on {
    color: deeppink;
    text-decoration: underline;
  }
  p{
    font-size: 35px;
  }
  .app_width{
    width: 100%;
  }

  button{
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-user-modify: read-write-plaintext-only;
    outline: none;
  }
  /*img{*/
    /*border-radius:35px;*/
  /*}*/

  .top-img{
    border-top-right-radius:28px;
    border-top-left-radius:28px
  }
</style>
<template>
  <div style="padding: 20px;overflow: hidden;background:#1b1a1f;z-index: 0">
    <div class="app_width slide-show" style="z-index: 10;border-top-right-radius:28px;border-top-left-radius:28px">
      <div class="slide-img">
        <a :href="slides[nowIndex].href">
          <transition name="slide-trans">
            <img class="top-img" img v-if="isShow" :src="slides[nowIndex].src">
          </transition>
          <transition name="slide-trans-old">
            <img class="top-img"  v-if="!isShow" :src="slides[nowIndex].src">
          </transition>
        </a>
      </div>
    </div>
    <div class="app_width" style="font-weight:bold; padding:25px; width: 100%; border-bottom:2px solid #000;background:#FFFFFF;border-bottom-left-radius:28px;border-bottom-right-radius:28px;">
      <P style="color: #888800;font-size: 40px">单位介绍</P>
      <P style="margin-left: 40px;margin-top: 20px;font-weight: bold">单位名称：<span style="font-size: 30px">{{baseInfo.orgName}}</span></P>
      <P style="margin-left: 40px">建筑名称：<span style="font-size: 30px">{{baseInfo.buildingName}}</span></P>
      <P style="margin-left: 40px">地    址：<span style="font-size: 30px">{{baseInfo.address}}</span></P>
      <button @click="getTask"  style="text-align:center; margin-top: 15px;margin-left: 210px;padding: 0px;border:0"><img src="../assets/report.png" style="width: 450px;"/></button>
    </div>
    <div class="app_width" style="margin-top:15px;">
      <img src="../assets/address.png" style="width: 100%;height: 595px"/>
      <div>
        <div style="width: 30%;position: absolute;top:1215px;left:60px"><img src="../assets/company.png" style="width: 210px;"/>
        </div>
        <div style="width: 65%;position: absolute;top:1215px;left:292px;height: 300px;">
          <div id="yun_dun_map" style="height: 100%;"></div>
          <!--<el-amap ref="map" vid="amapDemo" :amap-manager="amapManager" :center="center" :zoom="zoom" :plugin="plugin" :events="events" class="amap-demo">-->
            <!--<el-amap-marker :position="center" title="网络产业大厦"></el-amap-marker>-->
          <!--</el-amap>-->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import server from "../../../config/api";
  import Vue from 'vue';

  export default {
    props: {
      slides: {
        type: Array,
        default: []
      },
      inv: {
        type: Number,
        default: 10000
      }
    },
    data () {
      return {
        baseInfo:{},
        buildingId:undefined,
        newRepair:undefined,
        nowIndex: 0,
        isShow: true,
        zoom: 18,
        center: [121.530506, 38.862402],
      }
    },
    computed: {
      prevIndex () {
        if (this.nowIndex === 0) {
          return this.slides.length - 1
        }
        else {
          return this.nowIndex - 1
        }
      },
      nextIndex () {
        if (this.nowIndex === this.slides.length - 1) {
          return 0
        }
        else {
          return this.nowIndex + 1
        }
      }
    },
    methods: {
      goto (index) {
        this.isShow = false;
        setTimeout(() => {
          this.isShow = true;
          this.nowIndex = index;
        }, 10);
      },
      runInv () {
        this.invId = setInterval(() => {
          this.goto(this.nextIndex)
        }, this.inv)
      },
      clearInv () {
        clearInterval(this.invId)
      },
      initData: function() {
        window.vm = undefined;
        let toolBar = new AMap.ToolBar({
          visible: true
        });
        let map = new AMap.Map("yun_dun_map", {
          resizeEnable: true,
//          mapStyle: 'amap://styles/grey'//样式URL
        });
        map.addControl(toolBar);
        let marker = new AMap.Marker({
          icon: "http://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
          position: this.center,
          zIndex: 150,
          map: map
        });
        map.setFitView();
      },
      getTask:function () {
        this.$router.push(
          {
            path: '/danger',query:{buildingId:this.buildingId,newRepair:this.newRepair}
          });
      },
      getBuildingInfo:function () {
        this.$http.get(server.api.app.getBuildingInfo.replace('{buildingId}', this.buildingId)).then((response)=>{
          this.baseInfo = response.body.data;
        },(error)=>{
          this.$Message.error('建筑基本信息获取失败!');
        });
      },
    },
    mounted () {
      document.title = '单位/建筑名称';
      this.buildingId= this.$route.query.id;
      this.newRepair=this.$route.query.newRepair;
      this.getBuildingInfo();
      this.runInv();
      window.vm = this;
      let script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = 'https://webapi.amap.com/maps?v=1.3&key=1302d47c18fb5b253bf03abf39047974&plugin=AMap.Scale,AMap.ToolBar&callback=window.vm.initData';  // 高德地图
      document.body.appendChild(script);
      setTimeout(() => {
        // 关闭开放的vue实例
        window.vm = undefined;
      }, 30 * 1000);
    }
  }
</script>





